import React, { Component } from "react";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

//const LoginForm = ({ match }) => {
class LoginForm extends Component {
  isLocalTokenValid = () => {
    const token = localStorage.getItem("reactCloudAppJwt");

    if (token !== null && token !== "undefined") {
      return true;
    }

    return false;
  };

  logoutFn = () => {
    const token = localStorage.getItem("reactCloudAppJwt");

    if (token !== null && token !== "undefined") {
      localStorage.removeItem("reactCloudAppJwt");
      console.log("token removed");
    }
  };

  clearInputData = ()=> {
    this.props.clearInputData();
  };

  render() {
    //this.clearInputData();
    const isLoggedIn = this.isLocalTokenValid();
    let renderDiv;

    if (!isLoggedIn) {
      renderDiv = (
        <div>
          {" "}
          <table className="logintable">
            <tbody>
              <tr>
                <td>Email: </td>
                <td>
                  <input
                    type="text"
                    name="email"
                    onChange={e => this.props.onChildDataEntry(e)}
                  />
                </td>
              </tr>
              <tr>
                <td>Password: </td>
                <td>
                  <input
                    type="password"
                    name="password"
                    onChange={e => this.props.onChildDataEntry(e)}
                  />
                </td>
              </tr>
              <tr>
                <td />
                <td>
                  <button
                    className="btn btn-primary btn-sm"
                    onClick={this.props.login}
                  >
                    Login
                  </button>
                </td>
              </tr>
            </tbody>
          </table>
          <ToastContainer autoClose={false} />
        </div>
      );
    } else {
      renderDiv = (
        <div>
          User is logged in. Click here to{" "}
          <a href="#" onClick={this.logoutFn}>
            logout
          </a>
        </div>
      );
    }

    return <div className="loginform">{renderDiv}</div>;
  }
}

