import React, { Component } from "react";
import { BrowserRouter as Router, Link, NavLink } from "react-router-dom";

class NavBar extends Component {
  render() {
    return (
      <div>
        <nav className="navbar navbar-expand-lg">
          <table className="navtable">
            <tbody>
              <tr>
                <td>
                  <NavLink className="navtablenavlink" to="/maindata">
                    RENTALS
                  </NavLink>
                </td>
                <td>
                  <NavLink className="navtablenavlink" to="/register">
                    REGISTER
                  </NavLink>
                </td>
                <td>
                  <NavLink className="navtablenavlink" to="/login">
                    LOGIN
                  </NavLink>
                </td>
              </tr>
            </tbody>
          </table>
        </nav>
      </div>
    );
  }
}

export default NavBar;
