import React, { Component } from "react";
import NavBar from "./components/navbar";
import SideNav from "./components/sidenav";
import SearchByPrice from "./components/searchbyprice";
import Rentals from "./components/rentals";
import "./App.css";
import axios from "axios";
import { number } from "prop-types";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import RegisterForm from "./components/register";
import LoginForm from "./components/login";
import { toast } from "react-toastify";

class App extends Component {
  authToastId = null;
  noLoginToastId = null;
  constructor(props) {
    super(props);
    this.state = {
      rentalData: [],
      distinctCities: [],
      minprice: 0,
      maxprice: 0,
      firstname: "",
      lastname: "",
      email: "",
      password: "",
      message: ""
    };
  }

  componentDidMount() {
    this.getAllRental();
    this.getDistinctCities();
  }

  componentWillUnmount() {}

  // fetch based - works fine
  /*getAllRental = () => {
    console.log(
      "<in getAllRental>localStored jwt: " +
        localStorage.getItem("reactCloudAppJwt")
    );
    this.setState({ minprice: "", maxprice: "" });
    fetch("/api/getAllRental")
      .then(data => data.json())
      .catch(error => {
        console.log("entered catch block of getAllRental");
        this.setState({ rentalData: [] });
        return Promise.reject();
      })
      .then(res => this.setState({ rentalData: res.data }));
  };*/

  isLocalTokenValid = () => {
    toast.dismiss(this.noLoginToastId);
    const token = localStorage.getItem("reactCloudAppJwt");

    if (token === null || !token || token === "undefined") {
      console.log("token is empty");

      this.noLoginToastId = toast.warning(
        "You should be logged in to use these features",
        {
          position: toast.POSITION.TOP_RIGHT
        }
      );

      return false;
    }

    return true;
  };

  getAllRental = () => {
    this.setState({ minprice: "", maxprice: "" });

    if (this.isLocalTokenValid()) {
      fetch("/api/getAllRental", {
        method: "GET", // *GET, POST, PUT, DELETE, etc.
        headers: {
          "Content-Type": "application/json; charset=utf-8",
          "Access-Control-Allow-Origin": "*",
          Authorization: localStorage.getItem("reactCloudAppJwt")
          // "Content-Type": "application/x-www-form-urlencoded",
        }
        //body: pmtOnFileReq // body data type must match "Content-Type" header
      })
        .then(data => data.json())
        .catch(error => {
          console.log("entered catch block of getAllRental");
          this.setState({ rentalData: [] });
          return Promise.reject();
        })
        .then(res => this.setState({ rentalData: res.data }));
    } else {
      this.setState({
        rentalData: []
      });
    }
  };

  // axios based : not working
  /*getAllRental = () => {
    this.setState({ minprice: "", maxprice: "" });
    axios({
      method: "get",
      url: "/api/getAllRental"
    }).then(res => {
      res = res.json();
      this.setState({ rentalData: res.data });
    });
  };
*/
  getDistinctCities = () => {
    fetch("/api/getDistinctCities")
      .then(data => data.json())
      .then(res => this.setState({ distinctCities: res.data }));
  };

  getRentalsByCity = cityName => {
    this.setState({ minprice: "", maxprice: "" });

    if (this.isLocalTokenValid()) {
      fetch("/api/getRentalBy?city=" + cityName, {
        method: "GET", // *GET, POST, PUT, DELETE, etc.
        headers: {
          "Content-Type": "application/json; charset=utf-8",
          "Access-Control-Allow-Origin": "*",
          Authorization: localStorage.getItem("reactCloudAppJwt")
          // "Content-Type": "application/x-www-form-urlencoded",
        }
        //body: pmtOnFileReq // body data type must match "Content-Type" header
      })
        .then(data => data.json())
        .then(res => this.setState({ rentalData: res.data }));
    } else {
      this.setState({
        rentalData: []
      });
    }
  };

  onChildDataEntry = event => {
    this.setState({
      [event.target.name]: event.target.value
    });
  };

  handleSearchByPrice = () => {
    if (this.isLocalTokenValid()) {
      fetch(
        "/api/getRentalByPriceRange?minprice=" +
          this.state.minprice +
          "&maxprice=" +
          this.state.maxprice,
        {
          method: "GET", // *GET, POST, PUT, DELETE, etc.
          headers: {
            "Content-Type": "application/json; charset=utf-8",
            "Access-Control-Allow-Origin": "*",
            Authorization: localStorage.getItem("reactCloudAppJwt")
            // "Content-Type": "application/x-www-form-urlencoded",
          }
          //body: pmtOnFileReq // body data type must match "Content-Type" header
        }
      )
        .then(data => data.json())
        .then(res => this.setState({ rentalData: res.data }));
    }
  };

  clearInputData = () => {
    this.setState({
      firstname: "",
      lastname: "",
      email: "",
      password: ""
    });
    console.log("<after clearing> email: " + this.state.email);
  };

  register = () => {
    toast.dismiss(this.authToastId);
    const registerdata = {
      firstname: this.state.firstname,
      lastname: this.state.lastname,
      email: this.state.email,
      password: this.state.password
    };

    console.log("before sending reg request: " + registerdata.email);

    axios
      .post("/api/register", registerdata)
      .then(res => {
        //console.log("success: " + res.data.success);

        this.setState({ message: res.data.message });
        if (!toast.isActive(this.authToastId)) {
          if (res.data.success) {
            localStorage.setItem("reactCloudAppJwt", res.data.token);
            this.authToastId = toast.success("" + res.data.message, {
              position: toast.POSITION.TOP_RIGHT
            });
            this.clearInputData();
          } else {
            this.authToastId = toast.info("" + res.data.message, {
              position: toast.POSITION.TOP_RIGHT
            });
          }
        }
      })
      .catch(error => {
        toast.warning(this.state.message, {
          position: toast.POSITION.TOP_RIGHT
        });
      });
  };

  login = () => {
    toast.dismiss(this.authToastId);

    const logindata = {
      email: this.state.email,
      password: this.state.password
    };

    axios
      .post("/api/login", logindata)
      .then(res => {
        //console.log("success: " + res.data.success);

        this.setState({ message: res.data.message });
        if (!toast.isActive(this.authToastId)) {
          if (res.data.success) {
            localStorage.setItem("reactCloudAppJwt", res.data.token);
            this.authToastId = toast.success("" + res.data.message, {
              position: toast.POSITION.TOP_RIGHT
            });
            this.clearInputData();
          } else {
            this.authToastId = toast.info("" + res.data.message, {
              position: toast.POSITION.TOP_RIGHT
            });
          }
        }
      })
      .catch(error => {
        toast.warning(this.state.message, {
          position: toast.POSITION.TOP_RIGHT
        });
      });
  };

  render() {
    return (
      <div>
        <NavBar />
        <br />
        <Switch>
          <Route
            exact
            path="/maindata"
            render={props => (
              <Rentals
                rentalData={this.state.rentalData}
                onChildDataEntry={this.onChildDataEntry}
                handleSearchByPrice={this.handleSearchByPrice}
                distinctCities={this.state.distinctCities}
                getRentalsByCity={this.getRentalsByCity}
                getAllRental={this.getAllRental}
                minprice={this.state.minprice}
                maxprice={this.state.price}
                message={this.state.message}
                {...props}
              />
            )}
          />
          <Route
            exact
            path="/register"
            render={props => (
              <RegisterForm
                onChildDataEntry={this.onChildDataEntry}
                register={this.register}
                clearInputData ={this.clearInputData}
                {...props}
              />
            )}
          />
          <Route
            exact
            path="/login"
            render={props => (
              <LoginForm
                onChildDataEntry={this.onChildDataEntry}
                login={this.login}
                clearInputData = {this.clearInputData}
                {...props}
              />
            )}
          />
        </Switch>
      </div>
    );
  }
}

export default App;
