import React, { Component } from "react";
// with es6
import { BootstrapTable, TableHeaderColumn } from "react-bootstrap-table";
import SearchByPrice from "./searchbyprice";
import SideNav from "./sidenav";
import styled from "styled-components";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

// with es5
//var ReactBsTable = require("react-bootstrap-table");
//var BootstrapTable = require("react-bootstrap-table").BootstrapTable;
//var TableHeaderColumn = require("react-bootstrap-table").TableHeaderColumn;

//var React = require("react");
//var ReactDOM = require("react-dom");

class Rentals extends Component {
  render() {
    const options = {
      page: 1, // which page you want to show as default
      sizePerPageList: [
        {
          text: "5",
          value: 5
        },
        {
          text: "10",
          value: 10
        },
        {
          text: "All",
          value: this.props.rentalData.length
        }
      ], // you can change the dropdown list for size per page
      sizePerPage: 5, // which size per page you want to locate as default
      pageStartIndex: 1, // where to start counting the pages
      paginationSize: 3, // the pagination bar size.
      prePage: "Prev", // Previous page button text
      nextPage: "Next", // Next page button text
      firstPage: "First", // First page button text
      lastPage: "Last", // Last page button text
      paginationShowsTotal: this.renderShowsTotal, // Accept bool or function
      paginationPosition: "bottom" // default is bottom, top and both is all available
      // hideSizePerPage: true > You can hide the dropdown for sizePerPage
      // alwaysShowAllBtns: true // Always show next and previous button
      // withFirstAndLast: false > Hide the going to First and Last page button
    };

    return (
      <div>
        <SideNav
          distinctCities={this.props.distinctCities}
          getRentalsByCity={this.props.getRentalsByCity}
          getAllRental={this.props.getAllRental}
        />
        <div className="rentalSection">
          <SearchByPrice
            onChildDataEntry={this.props.onChildDataEntry}
            handleSearchByPrice={this.props.handleSearchByPrice}
          />
          <br />
          <br />
          <BootstrapTable
            data={this.props.rentalData}
            striped
            hover
            pagination={true}
            options={options}
          >
            <TableHeaderColumn dataField="room_id" isKey dataSort={true}>
              Room_id
            </TableHeaderColumn>
            <TableHeaderColumn dataField="neighborhood" dataSort={true}>
              Neighborhood
            </TableHeaderColumn>
            <TableHeaderColumn dataField="room_type" dataSort={true}>
              Room_Type
            </TableHeaderColumn>
            <TableHeaderColumn dataField="country" dataSort={true}>
              Country
            </TableHeaderColumn>
            <TableHeaderColumn dataField="city" dataSort={true}>
              City
            </TableHeaderColumn>
            <TableHeaderColumn dataField="reviews" dataSort={true}>
              Reviews
            </TableHeaderColumn>
            <TableHeaderColumn dataField="overall_satisfaction" dataSort={true}>
              Satisfaction
            </TableHeaderColumn>
            <TableHeaderColumn dataField="accommodates" dataSort={true}>
              Accommodates
            </TableHeaderColumn>
            <TableHeaderColumn dataField="bedrooms" dataSort={true}>
              Bedrooms
            </TableHeaderColumn>
            <TableHeaderColumn dataField="bathrooms" dataSort={true}>
              Bathrooms
            </TableHeaderColumn>
            <TableHeaderColumn dataField="price" dataSort={true}>
              Price
            </TableHeaderColumn>
          </BootstrapTable>
        </div>
        <ToastContainer autoClose={false} />
      </div>
    );
  }
}

export default Rentals;
