import React, { Component } from "react";

class SearchByPrice extends Component {
  render() {
    return (
      <React.Fragment>
        <span className="searchlabel">Search : </span>
        <br />
        <span className="searchlabel">Minimum price: </span>
        <input
          type="text"
          name="minprice"
          value={this.props.minprice}
          onChange={e => this.props.onChildDataEntry(e)}
        />
        {"  "}
        <span className="searchlabel"> Maximum price: </span>{" "}
        <input
          type="text"
          name="maxprice"
          value={this.props.maxprice}
          onChange={e => this.props.onChildDataEntry(e)}
        />
        {"  "}
        {"  "}
        {"  "}
        <button
          className="searchbutton"
          onClick={this.props.handleSearchByPrice}
        >
          Search
        </button>
      </React.Fragment>
    );
  }
}

export default SearchByPrice;
